.cart-dropdown {
	position: absolute;
	width: 300px;
	height: 340px;
	display: flex;
	flex-direction: column;
	padding: 20px;
	border: 1px solid black;
	background-color: white;
	top: 90px;
	right: 40px;
	z-index: 5;
	overflow: scroll;

	.empty-message {
		font-size: 20px;
		display: flex;
		justify-content: center;
		margin: 100px auto;
	}

	.cart-items {
		height: 240px;
		display: flex;
		flex-direction: column;
	}

	button {
		margin-top: auto;
		font-size: 10px;
	}
}
