.checkout-page {
	width: 60%;
	min-height: 90vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 50px auto 0;

	.checkout-header {
		width: 100%;
		padding: 10px;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid darkgrey;

		.header-block {
			font-weight: bold;
			width: 23%;

			&:last-child {
				width: 8%;
				margin-right: 5px;
			}
		}
	}

	.total {
		margin-top: 30px;
		margin-left: auto;
		font-size: 36px;
	}
}
